  padding: 12px;
  background-color: var(--secondary);
  color: var(--white1);
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
  margin-bottom: 0px;
  box-sizing: border-box;
}

.nav__logo {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  pointer-events: none;
}

.nav__logo img {
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.nav-link {
  display: block;
  padding: 12px 18px;
  text-decoration: none;
  color: var(--black1);
}

.nav-link:hover {
  background: var(--primary);
  border-radius: 10px;
}

.main-nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.main-nav ul li a {
  color: var(--black1);
  text-decoration: none;
}

.form-section-tinder {
  width: 50%;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.footer-text {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  margin-top: auto;
  gap: 1em;
  color: var(--grey2);
}

.footer-text a {
  color: var(--secondary);
  text-decoration: none;
}

.footer-text a:hover {
  text-decoration: underline;
}

.image-section-wrapper {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-section {
