  width: 100%;
  padding: 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  margin-bottom: 16px;
  font-size: 14px;
  box-sizing: border-box;
}

button {
  width: 100%;
  padding: 12px;
  background-color: var(--secondary);
  color: var(--white1);
  border: none;
  border-radius: 6px;
  cursor: pointer;
