  height: 80px;
  border: none;
  background: var(--secondary);
  display: inline-block;
  margin: 0 8px;
  justify-content: center;
  align-items: center;
  position: relative;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

