  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.custom-btn img {
  width: 75%;
  height: 75%;
  object-fit: contain;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.tinder {
  width: 100vw;
  height: 92vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
  opacity: 1;
  transition: opacity 0.1s ease-in-out;
}

.loaded.tinder {
  opacity: 1;
}

.tinder--cards {
  flex-grow: 1;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  z-index: 1;
}

.tinder--card {
  padding: 20px;
  display: inline-block;
  width: 65vw;
  height: 75vh;
  background: var(--white1);
  border-radius: 12px;
  overflow: hidden;
  position: absolute;
  will-change: transform;
  transition: all 0.3s ease-in-out;
  cursor: grab;
  display: flex;
  user-select: none;
  opacity: 1;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

.image-section-tinder {
  width: 50%;
  background-color: var(--secondary);
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  pointer-events: none;
}

.image-section-tinder img {
  height: 100%;
  object-fit: cover;
  user-select: none;
  object-position: center;
  pointer-events: none;
}

*, *:before, *:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.moving.tinder--card {
  transition: none;
  cursor: grabbing;
}

.tinder--card p {
  font-size: 20px;
  padding: 0 16px;
  pointer-events: none;
}

.tinder--buttons button:focus {
  outline: 0;
}

.tinder--buttons i {
  font-size: 32px;
  vertical-align: middle;
}

.fa-heart img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  position: absolute;
}

.gender-selection {
  display: flex;
  align-items: center;
}

.gender-selection input[type=radio] {
  appearance: none;
  -webkit-appearance: none;
  background-color: #fff;
  border: 2px solid var(--secondary, #49b2a6);
  border-radius: 50%;
  width: 20px;
  height: 20px;
  position: relative;
  cursor: pointer;
  outline: none;
  margin-right: 10px;
}

.gender-selection input[type=radio]:checked::before {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: var(--secondary, #49b2a6);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.gender-selection label {
  margin-right: 20px;
  color: var(--secondary, #49b2a6);
}

input[type=range] {
  -webkit-appearance: none;
  width: 100%;
  height: 8px;
  background: #ddd;
  border-radius: 5px;
  outline: none;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background: var(--secondary);
  border-radius: 50%;
  cursor: pointer;
}

input[type=range]::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: var(--secondary);
  border-radius: 50%;
  cursor: pointer;
}

output {
  margin-top: 10px;
